'use client';

import Image from 'next/image';
import Link from 'next/link';

type HeroProps = {
  title?: string;
  blurb?: string;
  ctaLabel?: string;
  ctaHref?: string;
  imageSrc?: string;
  imageAlt?: string;
};

export default function Hero({
  title = 'Элегантно\nНежный,\nСоздан\nДля Вас',
  blurb = 'Seasonless pieces made to move with you.',
  ctaLabel = 'Узнать больше',
  ctaHref,
  imageSrc = '/hero_banner.jpg',
  imageAlt = 'Model wearing a linen set',
}: HeroProps) {
  const Title = () => (
    <h1 className="whitespace-pre-wrap text-5xl leading-[0.95] tracking-tight font-semibold md:text-7xl xl:text-[80px] 2xl:text-[96px]">
      {title}
    </h1>
  );

  const CTA = () =>
    ctaHref ? (
      <Link
        href={ctaHref}
        className="inline-flex items-center rounded-md px-5 py-2.5 text-sm font-medium ring-1 ring-neutral-900 bg-neutral-900 text-white hover:opacity-90 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-neutral-900"
        aria-label={ctaLabel}
      >
        {ctaLabel}
      </Link>
    ) : (
      <button
        type="button"
        className="inline-flex items-center rounded-md px-5 py-2.5 text-sm font-medium ring-1 ring-neutral-900 bg-neutral-900 text-white opacity-95 hover:opacity-100 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-neutral-900"
        aria-label={ctaLabel}
      >
        {ctaLabel}
      </button>
    );

  return (
    <section className="relative w-full">
      {/* Responsive container — expands on ultrawide screens */}
      <div className="mx-auto w-full px-4 sm:px-6 lg:px-8 max-w-7xl xl:max-w-[1800px] 2xl:max-w-[2000px]">
        {/* 12-column grid for layout flexibility */}
        <div className="grid grid-cols-1 items-start gap-y-10 py-16 md:grid-cols-13 md:gap-x-8 lg:py-24">
          {/* Left: Title */}
          <div className="md:col-span-4 lg:col-span-4">
            <Title />
          </div>

          {/* Middle: Image */}
          <div className="md:col-span-5 lg:col-span-5 md:order-none">
            <div className="relative mx-auto w-full max-w-[520px] xl:max-w-[640px] 2xl:max-w-[720px]">
              <div className="overflow-hidden rounded-2xl bg-neutral-200">
                <Image
                  src={imageSrc}
                  alt={imageAlt}
                  width={1040}
                  height={1300}
                  className="h-auto w-full object-cover"
                  priority
                />
              </div>
            </div>
          </div>

          {/* Right: blurb + CTA */}
          <div className="md:col-span-3 lg:col-span-3 md:mt-4">
            <p className="mb-4 text-sm text-neutral-700">{blurb}</p>
            <CTA />
          </div>
        </div>

        {/* Pink underline */}
        <div className="pb-10 lg:pb-16">
          <span className="mx-auto block h-[4px] w-8 rounded-full bg-pink-500"></span>
        </div>
      </div>
    </section>
  );
}
